$gradient: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56));
.header {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  padding-top: 45px;

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.header--home {
  padding-bottom: 95px;
  background-image: $gradient, url(/images/header-d-1x.jpg);

  @media screen and(min-width:$mobile) {
    background-image: $gradient, url(/images/header-m-1x.jpg);
    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: $gradient, url(/images/header-m-1x.jpg);
    }
  }
  @media screen and(min-width:$tablet) {
    background-image: $gradient, url(/images/header-t-1x.jpg);
    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: $gradient, url(/images/header-t-2x.jpg);
    }
  }
  @media screen and(min-width:$desktop) {
    background-image: $gradient, url(/images/header-d-1x.jpg);
    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: $gradient, url(/images/header-d-2x.jpg);
    }
  }
}

.header--library {
  padding-bottom: 65px;
  background-image: $gradient, url(/images/header-red-d-1x.jpg);

  @media screen and(min-width:$mobile) {
    background-image: $gradient, url(/images/header-red-m-1x.jpg);
    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: $gradient, url(/images/header-red-m-1x.jpg);
    }
  }
  @media screen and(min-width:$tablet) {
    background-image: $gradient, url(/images/header-red-t-1x.jpg);
    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: $gradient, url(/images/header-red-t-2x.jpg);
    }
  }
  @media screen and(min-width:$desktop) {
    background-image: $gradient, url(/images/header-red-d-1x.jpg);
    @media screen and (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: $gradient, url(/images/header-red-d-2x.jpg);
    }
  }
}
.container--flex {
  display: flex;
  flex-wrap: wrap;

  @media screen and (min-width: $tablet) {
    justify-content: space-between;
  }
}

.logo {
  cursor: pointer;
  display: flex;

  border: 0;
  padding: 0;
  background-color: inherit;
  @media screen and (min-width: $tablet) {
    display: flex;
    align-items: center;
  }
}
.logo__icon {
  fill: var(--white-text-color);
  height: 25px;
  width: 25px;
  //   &:hover {
  //     transform: rotate(45deg);
  //   }
}
.logo__text {
  display: none;
  @media screen and (min-width: $tablet) {
    display: inline;
    font-size: 30px;
    line-height: 35px;
    margin-left: 10px;

    color: var(--white-text-color);
  }
}
.navigation {
  display: flex;
  align-items: flex-end;
  @media screen and (max-width: $tablet) {
    margin-left: 110px;
  }
  @media screen and (min-width: $tablet) {
    margin-left: 305px;
  }
  @media screen and (min-width: $desktop) {
    margin-left: 565px;
  }
}
.navigatioon__item {
  &:not(:first-child) {
    margin-left: 40px;
  }
}
.navigation__btn {
  position: relative;
  display: block;
  cursor: pointer;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  padding: 0;
  color: var(--white-text-color);
  border: 0;

  background-color: inherit;
  &--current {
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -3px;
      display: block;
      width: 100%;
      height: 3px;
      background: var(--accent-color);
    }
  }
}

.search-field {
  position: relative;

  margin-top: 40px;
  margin-right: auto;
  margin-left: auto;
}

.search-field__input {
  cursor: pointer;
  width: 100%;
  color: var(--white-text-color);

  background-color: inherit;
  border: inherit;
  border-bottom: 0.5px solid var(--white-text-color);

  @media screen and (min-width: $tablet) {
    width: 335px;

    margin-right: auto;
    margin-left: auto;
  }
}
.search-field__svg {
  fill: var(--white-text-color);
  position: absolute;
  top: 3px;
  right: 3px;
}
.library-btns-container {
  display: flex;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}
.on-library-none {
  display: none;
}
.on-home-none {
  display: none;
}

.library-btn {
  cursor: pointer;
  text-transform: uppercase;
  color: var(--white-text-color);
  padding: 0;
  width: 130px;
  height: 44px;

  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: inherit;

  filter: drop-shadow(0px 8px 43px rgba(255, 107, 1, 0.6));
  @media screen and (min-width: $tablet) {
    width: 136px;
    height: 44px;
  }
  &:not(:first-child) {
    margin-left: 20px;
    @media screen and (min-width: $tablet) {
      margin-left: 15px;
    }
  }
}

.library-btn--current {
  border-color: var(--accent-color);
  background-color: var(--accent-color);
  box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
}
